import React from 'react';
import { Link } from 'react-router-dom';

// Ant Design
import { Button } from 'antd';
import { CheckOutlined } from '@ant-design/icons';

export default function SubscribeButton(props) {

  return (
    <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', zIndex: 100 }}>
      <Link to="/subscribe">
        <Button type="primary" className="subButton" style={{ backgroundColor: '#1890ff' }}>
          Subscribe today
        </Button>
      </Link>
      <div style={{
          display: 'flex',
          marginBottom: '1rem',
          alignItems: 'center',
          width: '100%',
          justifyContent: 'center',
          color: 'white'
        }}
      >
        <CheckOutlined style={{ fontSize: '75%', marginRight: '.25rem', color: 'gray' }} />
        <p style={{ margin: 0, color: props.color }}>1 month free</p>
        <CheckOutlined style={{ fontSize: '75%', marginRight: '.25rem', color: 'gray', marginLeft: '1rem' }} />
        <p style={{ margin: 0, color: props.color }}>Cancel anytime</p>
      </div>
    </div>
  );
}