import React, { useState } from 'react';
import { Typography, Modal, Button, Input } from 'antd';
import { SearchOutlined } from '@ant-design/icons';

const { Title } = Typography;

export default function Search(props) {

  const [ visible, setVisible] = useState(false);

  return (
    <div>
      <Button
        type="text"
        style={{ margin: '0 1rem' }}
        onClick={() => setVisible(true)}
      >Search</Button>
      <Modal
        className="searchClass"
        visible={visible}
        footer={null}
        onCancel={() => setVisible(false)}
      >
        <div className="searchCont">
          <Input prefix={<SearchOutlined style={{ color: '#006fcf' }} />} placeholder="Search" bordered={false} />
          <div className="searchSugg">
          <Title level={5}>SUGGESTIONS</Title>
            <div style={{ display: 'flex' }}>
              <div className="suggItem">Lorem Ipsum</div>
              <div className="suggItem">Dolorsit</div>
              <div className="suggItem">Sit Amet</div>
            </div>
          </div>
        </div>
      </Modal>
    </div>
  );
}
