import React from 'react';
import { Link } from 'react-router-dom';
import { Row, Typography, Col } from 'antd';

const { Title } = Typography;

const cards = [
  {title: 'Aggregate', link: 'categories/cement'},
  {title: 'Pipes', link: 'categories/pipes'},
  {title: 'Wood', link: 'categories/wood'},
  {title: 'Tiles', link: 'categories/tiles'},
  {title: 'Crushed Stone', link: 'categories/stone'},
  {title: 'Tools', link: 'categories/tools'},
];

export default function ViewCards(props) {

  return (
    <div>
      <Title style={{ fontWeight: 'bold', textAlign: 'center' }} level={2}>Explore by Popular Categories</Title>
      <Row gutter={[25, 25]} justify="center" className="linkRow">
      {
        cards.map((card) => (
          <Col xs={12} lg={8} className="cardLink">
            <Link to={`/${card.link}`}>
              <div className="linkClass">
                  <p>{card.title}</p>
              </div>
            </Link>
          </Col>
        ))
      }
        <Col xs={12} lg={6}>
          <Link to="/categories">
            <Title style={{ textAlign: 'center', margin: '2rem 0', color: '#006fcf' }} level={5}>
              View All Categories
            </Title>
          </Link>
        </Col>
      </Row>
    </div>
  );
}
