import React from 'react';
import { Link } from 'react-router-dom';
import { Row,Typography, Col, Card, Button } from 'antd';
import { ArrowRightOutlined } from '@ant-design/icons';
import { blue } from '@ant-design/colors';

import cement from '../../images/materials/cement.jpg'
import roof from '../../images/materials/roof.jpg'
import gravel from '../../images/materials/gravel.jpg'
import lumber from '../../images/materials/lumber.jpg'

import dangote from '../../images/materials/dangote.png';
import seal from '../../images/materials/seal.png';
import berger from '../../images/materials/berger.jpg';


const { Meta } = Card;
const { Title, Text } = Typography;

export default function ViewCards(props) {

  const markupItem = [
    {image: cement, itemName: 'Cement', material: 'Aggregate', title: 'Price Update - Dangote Cement', event: 'increase', date: '21 December 2020' },
    {image: roof, itemName: 'Gravel', material: 'Crushed Stone', title: 'Price Update - 1inch gravel', event: 'decrease', date: '11 December 2020' },
    // {itemName: 'PVC Pipes', material: 'Pipes', title: 'Price Update - PVC Pipes', event: 'increase', date: '23 November 2020' },
    {image: gravel, itemName: 'Steel', material: 'Steel & Metal', title: 'Location Update - 1inch Steel Rod', event: 'location', date: '10 November 2020' },
    // {itemName: 'BUA Cement', material: 'Cement', title: 'Price Update - BUA Cement', event: 'increase', date: '1 November 2020'},
    {image: lumber, itemName: 'Lumber', material: 'Wood', title: 'New Item - Samir Lumber', event: 'new', date: '22 October 2020'},
  ];

  const det = 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.'

  const markupMat = [
    {image: dangote, title: 'Dangote Cement', details: det, date: '31 December 2020'},
    {image: seal, title: 'Crushed Stone Nigeria', details: det, date: '18 December 2020'},
    {image: berger, title: 'Berger New Paint', details: det, date: '29 November 2020'},
  ];

  const display = props.details.kind === 'item' ? markupItem : (
    props.details.kind === 'material' ? markupMat : null
  );

  return (
    <div className="viewCard">
      <Title level={1}>{props.details.title}</Title>
      <Title style={{ color: 'gray' }} level={5}>{props.details.text}
        <Link to={`/${props.details.link}`}>
          Browse all <ArrowRightOutlined style={{ fontSize: '75%' }} />
        </Link>
      </Title>
      <Row gutter={[{sm: 20, lg: 10}, 16]} justify="center" style={{ margin: '2rem auto' }}>
        {
          display.map((dis, index) => (
            <Col
            key={index}
            xs={24}
            sm={12}
            md={props.details.kind === 'item' ? 6 : 8}>
{//              <Card
              //   hoverable
              //   style={{ padding: '1rem 0', height: '100%' }}
              // >
              //   <Meta
              //     avatar={dis.event !== undefined ? <Color color={dis.event} /> : <img src={dis.image} className="cardImg" /> }
              //     title={dis.event !== undefined ? dis.material : null}
              //   />
              //   <div className="cardBody">
              //     <Title style={{ fontWeight: '700', color: '#1f2430' }} level={4}>{ dis.event !== undefined ? dis.itemName : dis.title }</Title>
              //     { dis.event !== undefined ? <Text style={{ fontSize: '115%' }}>{dis.title}</Text> : <p>{dis.details}</p> }
              //     <blockquote style={{ paddingTop: dis.event !== undefined ? '5rem' : '1rem' }}>{dis.date}</blockquote>
              //   </div>
              // </Card>
}

              <Card
                style={{ padding: props.details.kind !== 'item' ? '1rem 0' : null, height: '100%' }}
                hoverable
                cover={<img className={props.details.kind === 'item' ? '' : 'cardImg'} alt="example" src={dis.image} />}
              >
                <Meta title={dis.event !== undefined ? dis.material : null} />
                <div className="cardBody">
                  <Title style={{ fontWeight: '700', color: '#1f2430' }} level={4}>{ dis.event !== undefined ? dis.itemName : dis.title }</Title>
                  { dis.event !== undefined ? <Text>{dis.title}</Text> : <p>{dis.details}</p> }
                  <blockquote style={{ marginTop: 'auto', paddingTop: dis.event !== undefined ? '2.5rem' : '1rem', color: 'lightgray' }}>{dis.date}</blockquote>
                </div>
              </Card>
            </Col>
          )
        )}
          <br />
          <Button type="primary"
            style={{
              padding: '1rem 2rem',
              height: 'auto',
              fontSize: '115%',
              margin: '2.5rem auto auto',
              backgroundColor: blue[7],
              borderColor: blue[7]
            }}
          >
            <b>
            {
              props.details.kind === 'item' ? 'Show All Updates' : 'Show All Posts'
            }
            </b>
          </Button>
      </Row>
    </div>
  );
}
