import React from 'react';

// Ant Design
import { Row, Col, Typography } from 'antd';
import { CheckCircleOutlined } from '@ant-design/icons';

// Comps
import SubscribeButton from '../components/layout/SubscribeButton';
import Newsletter from '../components/layout/Newsletter';
import Footer from '../components/layout/Footer';

// Images
import how1 from '../images/subscribe/how1.png';
import how2 from '../images/subscribe/how2.png';
import how3 from '../images/subscribe/how3.png';
import logo from '../images/ppText.png'

const { Title } = Typography;

const top = {
  background: 'white',
  position: 'absolute',
  top: 0,
  bottom: 0,
  left: 0,
  right: 0,
  zIndex: 1,
  margin: 0
}

const cont = {
  marginTop: '35vh',
  backgroundColor: 'white',
  display: 'flex',
  alignItems: 'flex-end',
  flexDirection: 'column'
}

const markup = [
  { image: how1, title: 'LOREM IPSUM SEARCH', subTitle: 'Be organized and consistent', text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore incididunt.'},
  { image: how2, title: 'LOREM IPSUM SELECT', subTitle: 'Plan and analyze your campaigns', text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore incididunt.'},
  { image: how3, title: 'LOREM IPSUM DATA', subTitle: 'Create smarter social media strategies', text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore incididunt.'},
];

const textMark = [
  { title: 'Lorem Ipsum', text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod' },
  { title: 'Lorem Ipsum', text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod' },
  { title: 'Lorem Ipsum', text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod' },
  { title: 'Lorem Ipsum', text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod' },
  { title: 'Lorem Ipsum', text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod' },
  { title: 'Lorem Ipsum', text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod' },
  { title: 'Lorem Ipsum', text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod' },
  { title: 'Lorem Ipsum', text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod' },
]

export default class how extends React.Component {

  render() {

    return (
      <div style={ top }>
        <div style={ cont }>
          <div className="howClass">
            <Title level={5} style={{ textTransform: 'uppercase', fontWeight: 'bold', color: '#1890ff' }}>
              lorem ipsum building
            </Title>
            <Title level={1} className="howHeader">
              Building materials for your business
            </Title>
            <p className="howTitle">
              Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniamquis nostrud exercitation ullamco.
            </p>
            <SubscribeButton color="black" />
          </div>
          <Col xs={22} md={20} lg={18} className="marginAuto">
          {
            markup.map((data, index) => (
            <Row key={index} justify="space-between" style={{ marginBottom: '2rem' }}>
              <Col xs={22} md={20} lg={{ span: 11, order: ((index === 0 || index === 2) ? 2 : (index === 1 ? 2 : 1)) }} className="marginAuto">          
                <Title level={5} style={{ textTransform: 'uppercase',  color: '#1890ff' }}>
                  {data.title}
                </Title>
                <Title level={2} style={{ fontWeight: 'bold', marginTop: '1rem' }}>
                  {data.subTitle}
                </Title>
                <p style={{ fontSize: '125%' }}>
                  {data.text}
                </p>
              </Col>
              <Col style={{ textAlign: 'center' }} order={(index === 0 || index === 2) ? 1 : (index === 1 ? 2 : 1)} lg={11}>
                <img className="howImg" src={data.image} alt="how-to" />
              </Col>
            </Row>
            ))
          }
          </Col>
        </div>
        <Col xs={24} md={19} className="marginAuto">
          <Row style={{ margin: '5rem auto auto auto' }}>
            <Col xs={22} md={20} lg={16} className="marginAuto">
              <Row justify="space-between">
                {
                  textMark.map((text, index)=> (
                  <Col xs={22} sm={11} key={index} style={{ display: 'flex' }}>
                    <CheckCircleOutlined style={{ fontSize: '150%', margin: '.2rem' }} />
                    <div style={{ marginLeft: '1rem' }}>
                      <Title level={5} style={{ fontWeight: 'bold' }}>{text.title}</Title>
                      <p style={{ fontSize: '115%' }}>{text.text}</p>
                    </div>
                  </Col>
                  ))
                }
              </Row>
            </Col>
            <Col xs={22} lg={8} style={{ margin: 'auto', textAlign: 'center' }}>
              <img src={logo} alt="logo" className="howlogo" />
            </Col>
          </Row>
        </Col>
        <Row style={{ margin: 0 }} gutter={[48, 8]}>
          <Col xs={23} lg={20} xl={19} style={{ margin: 'auto', overflow: 'hidden' }}>
            <Newsletter />
          </Col>
        </Row>
        <Footer />
      </div>
    );
  }
}
