import React from 'react';  

// Ant Design
import { Input, Carousel, Row, Col } from 'antd';

// Comps
import CatCard from '../components/layout/CatCard';
import ViewCards from '../components/layout/ViewCards';
import Newsletter from '../components/layout/Newsletter';
import Footer from '../components/layout/Footer';
import SubscribeButton from '../components/layout/SubscribeButton';

// Images
import carousel1 from '../images/home/carousel1.jpg';
import carousel2 from '../images/home/carousel2.jpg';
import carousel3 from '../images/home/carousel3.jpg';
import carousel4 from '../images/home/carousel4.jpg';

const { Search } = Input;

export default class home extends React.Component {

  render() {

    const images = [
      {image: carousel1},
      {image: carousel2},
      {image: carousel3},
      {image: carousel4},
    ]

    const details = [
      { kind: 'item', title: 'Recent Updates', text: 'Get the latest updates. ', link: 'updates' },
      { kind: 'material', title: 'Blog', text: 'Read all our blog posts. ', link: 'blog' }
    ];

    return (
        <div
          style={{
            background: 'white',
            position: 'absolute',
            top: 0,
            bottom: 0,
            left: 0,
            right: 0,
            zIndex: 1,
            paddingTop: '150px',
            margin: 0
        }}>
        <div className="homeClass">
        <Col xs={23} lg={20} xl={19} className="marginAuto">
          <h1 className="homeTitle">Discover different<br/>prices, places, and products</h1>
          <p className="titleSub">Welcome to the ultimate building guide</p>
        </Col>
        <div className="searchHome">
          <div style={{ padding: '2rem', position: 'relative' }}>
            <Col xs={24} md={12} lg={16} className="search">
              <Search
                placeholder="Item or keyword"
                allowClear
                enterButton="Search"
                size="large"
              />
            </Col>
            <Carousel
              autoplay
              effect="fade"
              dots={false}
              autoplaySpeed={10000}
            >
            {
              images.map((i) => (
                <div className="contentStyle">
                  <img src={i.image} alt="carouselSlide" className="carouselClass" />
                </div>
                ))
            }
            </Carousel>
          </div>
        </div>
        </div>
        <Row style={{ flexDirection: 'column', paddingTop: '10%', margin: 0 }} gutter={[48, 8]}>
          <Col xs={23} lg={20} xl={19} style={{ margin: 'auto', overflow: 'hidden' }}>
            <CatCard />
            <ViewCards details={details[0]} />
          </Col>
          <div>
            <div className="subscribe">
              <p style={{
                fontSize: '200%',
                fontWeight: '900',
                textTransform: 'uppercase',
                color: 'rgba(255, 255, 255)',
                opacity: .1
              }}
              >
                Cement Paint Tiles Roofing <span style={{ textTransform: 'lowercase' }}>and more...</span>
              </p>
              <h1>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore.</h1>
              <SubscribeButton color="white" />
            </div>
          </div>
          <Col xs={23} lg={20} xl={19} style={{ margin: 'auto', overflow: 'hidden' }}>
            <ViewCards details={details[1]} />
            <Newsletter />
          </Col>
        </Row>
        <Footer />
      </div>
    );
  }
}
