import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

// Ant Design
import { Menu, Button, Layout, Divider } from 'antd';

import MobileNav from './MobileNav';
import Search from './Search';

const { Header } = Layout;
const { SubMenu } = Menu;

export default function Navbar(props) {

  const [ width, setWidth] = useState(window.innerWidth);

  const handleResize = (e) => {
    setWidth({ windowWidth: window.innerWidth });
  };

  useEffect(() => {
    window.addEventListener("load", handleResize);
  }, []);

  const navMarkup = width.windowWidth < 900 ? <MobileNav /> : (
        <Menu mode="horizontal">

          <Link to="/" className="link-class">
            <div className="logo">
            </div>
          </Link>

          <Menu.Item>
            Recent Updates
          </Menu.Item>
          <SubMenu title="Categories" className="menuClass">
            <Menu.Item>Option 1</Menu.Item>
            <Menu.Item>Option 2</Menu.Item>
            <Menu.Item>Option 3</Menu.Item>
            <Menu.Item>Option 4</Menu.Item>
            <Menu.Item>
              <Link to="/categories">View All</Link>
            </Menu.Item>
          </SubMenu>
          <SubMenu title="Subscribe" className="menuClass">
            <Menu.Item>
              <Link to="/subscribe" className="link-class">View Plans</Link>
            </Menu.Item>
            <Menu.Item>
              <Link to="/how-to" className="link-class">How It Works</Link>
            </Menu.Item>
          </SubMenu>
          
          <Divider type="vertical" />
          
          <SubMenu title="About" className="menuClass">
            <Menu.Item>
              <Link to="/about">Who We Are</Link>
            </Menu.Item>
            <Menu.Item>
              <Link to="/locations">Locations</Link>
            </Menu.Item>
{/*            <Menu.Item>
              <Link to="/contribute">Contribute</Link>
            </Menu.Item>*/}
            <Menu.Item>
              <Link to="/contact">Contact</Link>
            </Menu.Item>
          </SubMenu>
          <Menu.Item>
            Blog
          </Menu.Item>

          <Divider type="vertical" />

          <div className="menuLeft">
            <Search />
            <Button type="text">
              <Link to="/login" className="link-class">
                Login
              </Link>
            </Button>
          </div>
        </Menu>) 

  return (
    <Layout style={{ zIndex: '10', position: 'relative' }}>
      <Header className="headerClass">
      {navMarkup }
      </Header>
    </Layout>
  );
}