import React from 'react';
import { Row, Card, Typography, Col } from 'antd';
import { WindowsOutlined, QqOutlined, AntDesignOutlined, SlackSquareOutlined, DribbbleOutlined, ApartmentOutlined } from '@ant-design/icons';

// Comps
import Newsletter from '../components/layout/Newsletter';
import Footer from '../components/layout/Footer';

const { Title } = Typography;

const catMarkup = [
  { title: 'Category 1', cover: <WindowsOutlined className="catIcon" /> },
  { title: 'Category 2', cover: <QqOutlined className="catIcon" /> },
  { title: 'Category 3', cover: <AntDesignOutlined className="catIcon" /> },
  { title: 'Category 4', cover: <SlackSquareOutlined className="catIcon" /> },
  { title: 'Category 5', cover: <DribbbleOutlined className="catIcon" /> },
  { title: 'Category 6', cover: <ApartmentOutlined className="catIcon" /> },
]

export default function Categories(props) {

  return (
    <div>
      <Col xs={24} md={20} className="marginAuto">
        <Row justify="center" gutter={[12, 56]} style={{ margin: 0, marginBottom: '5rem' }}>
        {
          catMarkup.map((cat) => (
            <Col xs={8} sm={5} lg={4} style={{ margin: 'auto 1rem' }}>
              <Card
                className="catCont"
                hoverable
                cover={cat.cover}
              >
                <Title level={5} className="catTitle" style={{ textAlign: 'center' }}>{cat.title}</Title>
              </Card>
            </Col>
          ))
        }
        {
          catMarkup.map((cat) => (
            <Col xs={8} sm={5} lg={4} style={{ margin: 'auto 1rem' }}>
              <Card
                className="catCont"
                hoverable
                cover={cat.cover}
              >
                <Title level={5} className="catTitle" style={{ textAlign: 'center' }}>{cat.title}</Title>
              </Card>
            </Col>
          ))
        }
        {
          catMarkup.map((cat) => (
            <Col xs={8} sm={5} lg={4} style={{ margin: 'auto 1rem' }}>
              <Card
                className="catCont"
                hoverable
                cover={cat.cover}
              >
                <Title level={5} className="catTitle" style={{ textAlign: 'center' }}>{cat.title}</Title>
              </Card>
            </Col>
          ))
        }
        </Row>
      </Col>
      <Row style={{ margin: 0 }} gutter={[48, 8]}>
        <Col xs={23} lg={20} xl={19} style={{ margin: 'auto', overflow: 'hidden' }}>
          <Newsletter />
        </Col>
      </Row>
      <Footer />
    </div>
  );
};
