import React from 'react';
import { Link } from 'react-router-dom';

// Ant Design
import { Card, Button, Row, Col, Typography } from 'antd';
import { CheckOutlined, RightCircleOutlined, FullscreenOutlined, FireOutlined, HeatMapOutlined, AntDesignOutlined, CopyrightOutlined } from '@ant-design/icons';

// Comps
import Newsletter from '../components/layout/Newsletter';
import Footer from '../components/layout/Footer';

const { Title } = Typography;

const top = {
  background: 'white',
  position: 'absolute',
  top: 0,
  bottom: 0,
  left: 0,
  right: 0,
  zIndex: 1,
  margin: 0
}

const planMark = [
  { color: '#62626c', type: 'Basic', title: 'For just getting started', price: '2,500', features: [ 'Lorem ipsum dolor sit amet', 'Lorem ipsum dolor sit amet cons', 'Lorem ipsum dolor sit' ] },
  { color: '#1e64f0', type: 'Advanced', title: 'For business building and growth', price: '5,000', features: [ 'Lorem ipsum dolor sit amet', 'Lorem ipsum dolor sit amet cons', 'Lorem ipsum dolor sit', 'Lorem ipsum dolor sit amet' ] },
  { color: '#fa4fa0', type: 'Pro', title: 'For investing and advertising', price: '10,000', features: [ 'Lorem ipsum dolor sit amet', 'Lorem ipsum dolor sit amet cons', 'Lorem ipsum dolor sit', 'Lorem ipsum dolor sit amet', 'Lorem ipsum dolor sit amet cons' ] }
]

const featMark = [
  { icon: <RightCircleOutlined style={{ color: '#62626c' }} className="iconSub" />, title: 'Fully customizable player', text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod.' },
  { icon: <FullscreenOutlined style={{ color: '#1e64f0' }} className="iconSub" />, title: 'Unlimited users', text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod.' },
  { icon: <FireOutlined style={{ color: '#fa4fa0' }} className="iconSub" />, title: 'Basic integrations', text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod.' },
  { icon: <HeatMapOutlined style={{ color: '#faaa29' }} className="iconSub" />, title: 'Easy embedding and sharing', text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod.' },
  { icon: <AntDesignOutlined style={{ color: '#57be8f' }} className="iconSub" />, title: 'Interactive video tools', text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod.' },
  { icon: <CopyrightOutlined style={{ color: '#b98be8' }} className="iconSub" />, title: 'Advanced video analytics', text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod.' },
]

const faqText = 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.';

const faqs = [
  { title: 'Privacy', questions: [
      { subtitle: 'Do I have to worry about you selling my email or data?', text: faqText },
    ]
  },
  { title: 'Plans and Features', questions: [
      { subtitle: 'What about if I purchase an annual plan?', text: faqText },
      { subtitle: 'What if I want to cancel my subscription?', text: faqText }
    ]
  },
  { title: 'My Account', questions: [
      { subtitle: 'What if I forget my login credentials?', text: faqText },
      { subtitle: 'Can I have multiple accounts?', text: faqText },
      { subtitle: 'Do I have to worry about my card details and information?', text: faqText }
    ]
  }  
]

export default class subscribe extends React.Component {

  render() {
    return (
      <div style={ top }>
        <div className="plans">
          <Col xs={24} sm={18} className="marginAuto">
            <Title className='planTitle' level={1}>Pick a plan to follow for your<br/>business and its growth</Title>
            <Row gutter={[48, 12]} style={{ margin: 0 }}>
              {
                planMark.map((plan, index) => (
                  <Col xs={24} sm={20} md={18} lg={8} key={index} className="marginAuto">
                    <div className="planCont">
                      <h1 style={{ color: plan.color }} >{plan.type}</h1>
                      <p>{plan.title}</p>
                      <Title
                        level={2}
                        style={{ display: 'flex', fontFamily: 'Candara', color: plan.color, justifyContent: 'center', alignItems: 'flex-end' }}
                      ><span style={{ fontFamily: 'system-ui' }}>₦</span>{plan.price} <p style={{ fontSize: '50%', color: 'gray', marginBottom: 5, opacity: .5 }}>/year</p></Title>
                      <Button shape="round" size="large" style={{ margin: '2rem 0 1rem', color: 'white', border: 0, backgroundColor: plan.color }}>Purchase Now</Button>
                      <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-start' }}>
                        <p style={{ margin: '2rem 0 1rem', color: 'black', fontWeight: 'bold' }}>All the features of the plan:</p>
                        {
                          plan.features.map((feat, i) => (
                            <small key={i}>
                              <ul style={{ padding: 0, maring: 0 }}>
                                <li style={{
                                  color: 'darkgray',
                                  display: 'flex',
                                  alignItems: 'center'
                                }}>
                                  <CheckOutlined style={{ marginRight: '1rem', color: 'black' }} /> {feat}
                                </li>
                              </ul>
                            </small>
                          ))
                        }
                      </div>
                    </div>
                  </Col>
                ))
              }
            </Row>
          </Col>
        </div>
        <Col xs={22} lg={18} className="marginAuto">
          <h4 style={{ fontWeight: 'bold', color: 'gray', margin: '5rem auto 2.5rem', textAlign: 'center' }}>PRICE PRO STANDARD FEATURES</h4>
          <Row style={{ margin: '2.5rem auto' }}>
            {
              featMark.map((feat) => (
                <Col xs={22} sm={11} lg={8} style={{ display: 'flex', flexDirection: 'col' }}>
                  {feat.icon}
                  <div style={{ padding: '.25rem' }}>
                    <Title level={5}>{feat.title}</Title>
                    <p style={{ color: 'gray', fontSize: '115%' }}>{feat.text}</p>
                  </div>
                </Col>
              )
            )}
          </Row>
          <Row gutter={[20, 20]} justify="space-between" style={{ margin: '7.5rem auto 0' }}>
            <Col xs={22} sm={20} md={11} className="marginAuto">
              <Card hoverable className="cardSub">
                <Title level={3}>Sign up yet? Join us today for lorem ipsum dolor sit amet, consectetur adipiscing elit. Sed elit consectetur do eiusmod.</Title>
                <Button type="link" size="large" style={{ padding: '1.5rem 0', fontSize: '150%' }}>
                  <Link to="/registration">Sign up today</Link>
                </Button>
              </Card>
            </Col>
            <Col xs={22} sm={20} md={11} className="marginAuto">
              <Card hoverable className="cardSub">
                <Title level={3}>Already have an account? Log in now lorem ipsum dolor sit amet, consectetur adipiscing elit. Sed do eiusmod.</Title>
                <Button type="link" size="large" style={{ padding: '1.5rem 0', fontSize: '150%' }}>
                  <Link to="/login">Login to subscribe</Link>
                </Button>
              </Card>
            </Col>
          </Row>
        </Col>
        <Row style={{ margin: 0 }} gutter={[48, 8]}>
          <Col xs={23} lg={20} xl={19} style={{ margin: 'auto', overflow: 'hidden' }}>
            <Newsletter />
          </Col>
        </Row>
        <Col xs={20} sm={20} lg={18} className="marginAuto">
          <h4 style={{ fontWeight: 'bold', color: 'gray', margin: '2.5rem auto', textAlign: 'center' }}>FREQUENTLY ASKED QUESTIONS</h4>
          <Row style={{ margin: '5rem auto' }} justify="space-between">
            {
              faqs.map((faq) => (
                <Col xs={24} sm={11} lg={7}>
                  <Title level={3} style={{ color: '#1e64f0' }}>{faq.title}</Title>
                  {
                    faq.questions.map((ques) => (
                      <div style={{ margin: '2rem 0' }}>
                        <Title level={5}>{ques.subtitle}</Title>
                        <p style={{ color: 'gray', fontSize: '110%' }}>{ques.text}</p>
                      </div>
                    ))
                  }
                </Col>
              ))
            }
          </Row>
        </Col>
        <Footer />
      </div>
    );
  }
}
