import React from 'react';

// Ant Design
import { Dropdown } from 'antd';

// Comps
import Footer from '../components/layout/Footer';

const top = {
  background: 'white',
  position: 'absolute',
  top: 0,
  bottom: 0,
  left: 0,
  right: 0,
  zIndex: 1,
  margin: 0
}

const items = [
  (<div className="stateClass">
    <ul>
      <li>Abba state</li>
      <li>Baba state</li>
      <li>Cacca state</li>
    </ul>
  </div>),
  (<div className="stateClass">
    <ul>
      <li>Abba state</li>
      <li>Baba state</li>
      <li>Cacca state</li>
    </ul>
  </div>),
  (<div className="stateClass">
    <ul>
      <li>Abba state</li>
      <li>Baba state</li>
      <li>Cacca state</li>
    </ul>
  </div>),
  (<div className="stateClass">
    <ul>
      <li>Abba state</li>
      <li>Baba state</li>
      <li>Cacca state</li>
    </ul>
  </div>),
  (<div className="stateClass">
    <ul>
      <li>Abba state</li>
      <li>Baba state</li>
      <li>Cacca state</li>
    </ul>
  </div>),
  (<div className="stateClass">
    <ul>
      <li>Abba state</li>
      <li>Baba state</li>
      <li>Cacca state</li>
    </ul>
  </div>)
]

export default class locations extends React.Component {

  render() {

    return (
      <div style={ top }>
        <div className="mapImg">
          <Dropdown placement="bottomCenter" overlay={items[0]}>
            <div className="northW aniDot">
              <button onClick={e => e.preventDefault()} className="smDot"></button>
            </div>
          </Dropdown>
          <Dropdown placement="bottomCenter" overlay={items[1]}>
            <div className="northE aniDot">
              <button onClick={e => e.preventDefault()} className="smDot"></button>
            </div>
          </Dropdown>
          <Dropdown placement="bottomCenter" overlay={items[2]}>
            <div className="northC aniDot">
              <button onClick={e => e.preventDefault()} className="smDot"></button>
            </div>
          </Dropdown>
          <Dropdown placement="topCenter" overlay={items[3]}>
            <div className="southW aniDot">
              <button onClick={e => e.preventDefault()} className="smDot"></button>
            </div>
          </Dropdown>
          <Dropdown placement="topCenter" overlay={items[4]}>
            <div className="southE aniDot">
              <button onClick={e => e.preventDefault()} className="smDot"></button>
            </div>
          </Dropdown>
          <Dropdown placement="topCenter" overlay={items[5]}>
            <div className="southS aniDot">
              <button onClick={e => e.preventDefault()} className="smDot"></button>
            </div>
          </Dropdown>
        </div>
        <Footer />
      </div>
    );
  }
}
