import React from 'react';
import { Row, Typography, Col, Button } from 'antd';

const { Title } = Typography;

export default function Newsletter(props) {

  return (
    <Row className="newsletter">
      <Col
        xs={{ span:24, offset:0 }}
        sm={{ span:16, offset:8 }}
        lg={{ span:12, offset:12 }}
      >
        <div>
          <Title level={2}>Get our newsletter</Title>
          <p>Stay on top of the latest information</p>
        </div>
        <Button>
          Subscribe
        </Button>
      </Col>
    </Row>
  );
}
