import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import Search from './Search'

// Ant Design
import { Menu, Button } from 'antd';
import { AlignLeftOutlined, CloseOutlined } from '@ant-design/icons';

const { SubMenu } = Menu;

export default function MobileNav(props) {

  const [collapsed, setCollapsed] = useState(true);

  const toggleCollapsed = () => {
    setCollapsed(!collapsed)
  };

  return (
    <div style={{ display: 'flex', width: '100%' }}>
    <Menu>

        <Link to="/" className="link-class">
          <div className="logo">
          </div>
        </Link>

        <Button type="text" onClick={toggleCollapsed} style={{ zIndex: 100, margin: '.5rem .5rem auto auto', fontSize: '1.25rem', background: 'none' }}>
          {collapsed ?
            <AlignLeftOutlined style={{ transform: 'rotate(180deg) scaleX(-1)' }} />
            :
            <CloseOutlined />}
        </Button>

        {
          collapsed ? null : (
            <div className="mobileClass">
            <Menu mode="inline">
              <Menu.Item onClick={toggleCollapsed}>
                Recent Updates
              </Menu.Item>
              <SubMenu title="Categories">
                <Menu.Item onClick={toggleCollapsed}>Category 1</Menu.Item>
                <Menu.Item onClick={toggleCollapsed}>Category 2</Menu.Item>
                <Menu.Item onClick={toggleCollapsed}>Category 3</Menu.Item>
                <Menu.Item onClick={toggleCollapsed}>Category 4</Menu.Item>
                <Menu.Item onClick={toggleCollapsed}>
                  <Link to="/categories">View All</Link>
                </Menu.Item>
              </SubMenu>
              <SubMenu title="Subscribe">
                <Menu.Item onClick={toggleCollapsed}>
                  <Link to="/subscribe">View Plans</Link>
                </Menu.Item>
                <Menu.Item onClick={toggleCollapsed}>
                  <Link to="/how-to">How It Works</Link>
                </Menu.Item>
                </SubMenu>
              <SubMenu title="About">
                <Menu.Item onClick={toggleCollapsed}>
                  <Link to="/about">Who We Are</Link>
                </Menu.Item>
                <Menu.Item onClick={toggleCollapsed}>
                  <Link to="/locations">Locations</Link>
                </Menu.Item>
{/*                <Menu.Item onClick={toggleCollapsed}>
                  <Link to="/contribute">Contribute</Link>
                </Menu.Item>*/}
                <Menu.Item onClick={toggleCollapsed}>
                  <Link to="/contact">Contact</Link>
                </Menu.Item>
              </SubMenu>
              <Search />
              <Button onClick={toggleCollapsed} type="text">
                <Link to="/login" className="link-class">
                  Login
                </Link>
              </Button>
            </Menu>
            </div>
          )
        }

      </Menu>
  </div>
  );
}