import React from 'react';

// Ant Design
import { Row, Col, Typography } from 'antd';
import { PicCenterOutlined, ScissorOutlined, BarChartOutlined, GlobalOutlined } from '@ant-design/icons';

// Comps
import Footer from '../components/layout/Footer';
import image from '../images/about/team.jpg'

const { Title } = Typography;

const top = {
  background: 'white',
  position: 'absolute',
  top: 0,
  left: 0,
  right: 0,
  zIndex: 1,
  margin: 0
}

const lorem = 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.'

const textMark = [
  { title: 'About Price Pro', text: lorem + ' ' + lorem, justify: 'end' },
  { title: 'Who is our audience?', text: lorem, justify: 'end' },
  { title: 'Our team', text: lorem, justify: 'start', image: image },
]

const valueMark = [
  { icon: <PicCenterOutlined style={{ textAlign: 'start', fontSize: '2.5rem' }} />, title: 'Salaries', text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit.' },
  { icon: <ScissorOutlined style={{ textAlign: 'start', fontSize: '2.5rem' }} />, title: 'Diversity', text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit.' },
  { icon: <BarChartOutlined style={{ textAlign: 'start', fontSize: '2.5rem' }} />, title: 'Open Source', text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit.' },
  { icon: <GlobalOutlined style={{ textAlign: 'start', fontSize: '2.5rem' }} />, title: 'Roadmap', text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit.' },
]

export default class about extends React.Component {

  render() {
    return (
      <div style={ top }>
        <div className="nameCont">
          <Col lg={8}>
            <p>Lorem ipsum dolor sit amet, cons ect etur adip iscing elit, sed do eiusmod tempor incididunt ut ipsum.</p>
          </Col>
        </div>
        <Col xs={22} md={15} style={{ margin: '5rem auto' }}>
        {
        textMark.map((data, index) => (
          <Row className="aboutRow" key={index} justify={data.justify}>
            {index > 1 ? <img src={image} className="aboutImg" alt="teamimage" /> : ''}
            <Col style={{ marginTop: '3rem' }} key={index} xs={22} sm={18} md={18}>
              <Title level={1} style={{ fontWeight: 'bold' }}>{data.title}</Title>
              <p style={{ fontSize: '125%' }}>{data.text}</p>
              {index > 0 ? <p style={{ fontSize: '125%' }}>{data.text}</p> : ''}
            </Col>
          </Row>
        ))
        }
        </Col>
        <div style={{ background: '#f9f9f9', padding: '2.5rem 0' }}>
          <Col xs={22} sm={20} lg={18} style={{ margin: '5rem auto' }}>
            <Col xs={22} md={14} className="aboutValues marginAuto">
              <Title style={{ fontWeight: 'bold' }} level={1}>Core Values</Title>
              <p style={{ fontSize: '125%', margin: '2rem auto' }}>We try our best to live up to our value of "Default to Transparency." Here's an overview of a few of the things we've made transparent at Buffer.</p>
            </Col>
            <Row justify="space-between">
              {
                valueMark.map((value) => (
                  <Col xs={22} sm={11} lg={5} className="marginAuto aboutValues" style={{ display: 'flex', flexDirection: 'column' }}>
                    {value.icon}
                    <Title style={{ fontWeight: 'bold', margin: '1rem 0' }} level={5}>{value.title}</Title>
                    <p style={{ fontSize: '110%' }}>{value.text}</p>
                  </Col>
                ))
              }
            </Row>
          </Col>
        </div>
        <Footer />
      </div>
    );
  }
}
