import React from 'react';
import { Link } from 'react-router-dom';

// Ant Design
import { Divider, Row, Typography, Col, Button } from 'antd';
import { FacebookOutlined, TwitterOutlined, InstagramOutlined, LinkedinOutlined } from '@ant-design/icons';

const { Title } = Typography;

export default function Footer(props) {

  return (
    <div className="footerClass">
      <Col xs={24} md={21} className="marginAuto">
        <Row justify="center">
          <Col xs={24} lg={9}>
            <div className="logo logoFooter">
            </div>
          </Col>
          <Col xs={24} sm={7} lg={5}>
            <Title level={5}>Categories</Title>
            <ul style={{ listStyle: 'none', padding: '1rem 0 2rem' }}>
              <li>
                <Link to="/categories">
                  Category 1
                </Link>
              </li>
              <li>
                <Link to="/categories">
                  Category 2
                </Link>
              </li>
              <li>
                <Link to="/categories">
                  Category 3
                </Link>
              </li>
              <li>
                <Link to="/categories">
                  Category 4
                </Link>
              </li>
              <li>
                <Link to="/categories">
                  Category 5
                </Link>
              </li>
              <li>
                <Link to="/categories">
                  View All
                </Link>
              </li>
            </ul>
          </Col>
          <Col xs={24} sm={7} lg={5}>
            <Title level={5}>Subscribe</Title>
            <ul style={{ listStyle: 'none', padding: '1rem 0 2rem' }}>
              <li>
                <Link to="/subscribe">
                  View Plans
                </Link>
              </li>
              <li>
                <Link to="/how-to">
                  How It Works
                </Link>
              </li>
            </ul>
          </Col>
          <Col xs={24} sm={7} lg={5}>
            <Title level={5}>About Us</Title>
            <ul style={{ listStyle: 'none', padding: '1rem 0 2rem' }}>
              <li>
                <Link to="/about">
                  Who We Are
                </Link>
              </li>
              <li>
                <Link to="/locations">
                  Locations
                </Link>
              </li>
{/*              <li>
                <Link to="/contribute">
                  Contribute
                </Link>
              </li>*/}
              <li>
                <Link to="/contact">
                  Contact
                </Link>
              </li>
            </ul>
          </Col>
        </Row>
        <hr style={{ borderColor: 'rgba(0,0,0,.05)' }} />
        <div className="footerBottom">
          <div className="footerIcons">
            <a style={{ color: 'black' }} href="http://www.instagram.com">
              <InstagramOutlined />
            </a>
            <a style={{ color: 'black' }} href="http://www.twitter.com">
              <TwitterOutlined />
            </a>
            <a style={{ color: 'black' }} href="http://www.facebook.com">
              <FacebookOutlined />
            </a>
            <a style={{ color: 'black' }} href="http://www.linkedin.com">
              <LinkedinOutlined />
            </a>
          </div>
          <div className="footerBtn">
            <Title level={5} style={{ margin: '1rem' }}><b>Sign up for Price Pro today</b></Title>
            <Button type="primary" size="large" style={{ borderRadius: '5px' }}>Sign up now</Button>
          </div>
        </div>
        <div>
          <small>Copyright © 2021 Price Pro</small>
          <Divider type="vertical" />
          <small>
            <Link style={{ color: 'black' }} to="/privacy">Privacy</Link>
          </small>
          <Divider type="vertical" />
          <small>
            <Link style={{ color: 'black' }} to="/terms">Terms</Link>
          </small>
        </div>
      </Col>
    </div>
  );
}
